import React, { useState, useEffect } from 'react'
import './index.css'
import Filter from './components/Filter'
import PersonForm from './components/PersonForm'
import PersonList from './services/PersonList'
import Notification from './components/Notification'
import PersonTable from './components/PersonTable'
import Persons from './components/Persons'

const App = () => {
  const [ persons, setPersons] = useState([
    { name: 'Arto Hellas', number: '040-123456' },
    { name: 'Ada Lovelace', number: '39-44-5323523' },
    { name: 'Dan Abramov', number: '12-43-234345' },
    { name: 'Mary Poppendieck', number: '39-23-6423122' }
  ])
  const [ newName, setNewName ] = useState('')
  const [ newNumber, setNewNumber ] = useState('')
  const [ findName, setFinder ] = useState('')
  const [message, setMessage] = useState(null)
  const [messageType, SetMessageType] = useState(null)


  useEffect(() => {
    PersonList
      .getAll()
      .then(initialPersons => {
        console.log('promise fulfilled')
        setPersons(initialPersons)
      })
  }, [])

  const addPerson = (event) => {
    event.preventDefault()
    console.log('button', event.target)
    const nameObject = {
      name: newName,
      number: newNumber,
      id: persons.length + 1,
    }
    if(persons.find(names => names.name === newName)) {
      if(window.confirm(`${newName} is already added to phonebook, replace the old number with a new one?`)) {
        const personToUpdate = persons.find(person => person.name.toLowerCase() === newName.toLowerCase())
        console.log('put löytyy')
        console.log(personToUpdate)
        PersonList
        .update(personToUpdate.id, { name: newName, number: newNumber})
        .then(returnedPerson => {
          setPersons(persons.map(person => person.id !== personToUpdate.id ? person : returnedPerson))
        })
        setNewName('')
        setNewNumber('')
        setMessage(`Succesfully updated ${newName}`)
        SetMessageType('success')
        setTimeout(() => {
          setMessage(null)
          SetMessageType(null)
        }, 5000)
        console.log('täällä')
      }
    }
    else
    PersonList
    .create(nameObject)
    .then(returnedPerson => {
      console.log(returnedPerson)
    setPersons(persons.concat(returnedPerson))
    setNewName('')
    setNewNumber('')
    setMessage(`Succesfully added ${newName}`)
    SetMessageType('success')
    setTimeout(() => {
      setMessage(null)
      SetMessageType(null)
    }, 5000)
    }
  )
  }
  const handleNameChange = (event) => {
    console.log(event.target.value)
    setNewName(event.target.value)
  }

  const handleFinderChange = (event) => {
    console.log(event.target.value)
    setFinder(event.target.value)
  }

  const handleNumberChange = (event) => {
    console.log(event.target.value)
    setNewNumber(event.target.value)
  }

  const removePerson = (id) => {
    const personToRemove = persons.find(person => person.id === id)
    PersonList
      .remove(id)
      .then(removedPerson => {
        console.log(removedPerson)
      setPersons(persons.splice(removedPerson))
      setFinder('')
      setMessage(`Succesfully removed ${personToRemove.name}`)
      SetMessageType('success')
      setTimeout(() => {
        setMessage(null)
        SetMessageType(null)
      }, 5000)
      })
    }
/*
    const personsToShow2 = persons.filter(person => person.name.toLocaleLowerCase().includes(findName))
    .map(person => <PersonTable name={person.name} number={person.number} removePerson={removePerson} />)
    console.log(personsToShow2)
*/
  
//<PersonTable persons={persons.filter((person) => findName === '' || person.name.toLowerCase().match(findName.toLowerCase()))} removePerson={removePerson}/>
    //const personsToShow = showAll
    return (
      <div>
        <h2>Phonebook</h2>
        <Notification message={message} className={messageType} />
        <Filter findName={findName} handleFinderChange={handleFinderChange} />
          <h3>add a new</h3>
        <PersonForm 
          addPerson={addPerson}
          newName={newName}
          handleNameChange={handleNameChange}
          newNumber={newNumber}
          handleNumberChange={handleNumberChange}
        />
        <h3>Numbers</h3>
        <PersonTable findName={findName} persons={persons.filter((person) => person.name.toLowerCase().match(findName.toLowerCase()))} removePerson={removePerson}/>


      </div>
    )

  }

export default App
