import React from 'react'

const PersonTable = ({persons, removePerson, findName}) => {
    if(findName === ''){
        return(
            <p>Search something</p>
        )
    }
    else{
    return (
        <div>
            <table>
                <tbody> 
                    {persons.map((person) => 
                    <Person key={person.name} 
                    name={person.name} 
                    number={person.number}
                    id={person.id}
                    removePerson={removePerson}
                    />)}
                </tbody>
            </table>
        </div>
        )
    }
}


const Person = ({ name, number, removePerson, id }) => {
    return (
      <tr><td>{name}</td><td>{number}</td><td><button onClick={() => {
          if(window.confirm(`Delete ${name}?`)){
          removePerson(id)}}}>Delete</button></td></tr>
    )
  }

    export default PersonTable
